.indexScroll {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 15px;
}
.indexScroll .scroll {
  grid-area: 1 / 1 / 3 / 3;
  border-radius: 4px;
  overflow: hidden;
}
.indexScroll .top {
  width: 100%;
  height: 0;
  padding-bottom: 56.75%;
  border-radius: 4px;
  background-color: rgba(0, 0, 0, 0.1);
  background-size: cover;
  background-position: center;
  cursor: pointer;
  overflow: hidden;
  position: relative;
}
.indexScroll .top span {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  line-height: 30px;
  padding: 0 15px;
  text-align: center;
  color: white;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0));
}
.list {
  background-color: rgba(0, 0, 0, 0.1);
  cursor: pointer;
  background-size: cover;
  background-position: center;
}
.list span {
  display: block;
  padding: 0 15px;
  height: 30px;
  line-height: 30px;
  color: white;
  font-size: 18px;
  background: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0));
}
