.index {
  min-height: calc(100vh - 260px);
}
.index .header {
  width: 100%;
  height: 360px;
  margin-bottom: 15px;
  background-size: cover;
  background-position: center;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.index .header .title {
  min-width: 35%;
  color: rgba(255, 255, 255, 0.8);
}
.index .header .title span {
  display: block;
  font-size: 36px;
  margin: 40px 0 20px 0;
}
.index .header .title p {
  font-size: 20px;
  margin-bottom: 0;
}
