.indexList {
  width: 100%;
}
.indexList .head {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 15px;
}
.indexList .head .left {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.indexList .head .left i {
  font-size: 26px;
  margin-right: 15px;
}
.indexList .head .left span {
  font-size: 18px;
}
